.header {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    margin-bottom: 5rem;
}
.login-logo {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 0.5rem;
    right: 0.5rem;
    width: 1.3rem;
    height: 1.3rem;
    border: 1px solid black;
    border-radius: 50%;
    text-decoration: none;
    font-weight: 300;
    font-size: 1.33rem;
    color: black;
}
.intro-text h1 {
    height: 6rem;
    display: flex;
    overflow: hidden;
    flex-direction: row;
}

.project-section .intro-text h1 {
    justify-content: center;
}

.intro-text h1 span {
    display: block;
    line-height: 6rem;
    font-size: 6rem;
    text-transform: uppercase;
    margin: 0.05rem;
    transform: translateY(-200%);
    color: black;
    font-family: Vollkorn;
}

.intro-text h1 span.active {
    transform: translateY(0%);
    transition: 2s ease-out;
}

.intro-text p {
    font-size: 1.5rem;
}
@media only screen and (max-width: 820px) {
    .intro-text h1 {
        height: 4rem;
        display: flex;
        overflow: hidden;
        /* margin-bottom: 1rem; */
        justify-content: center;
    }

    .intro-text h1 span {
        display: block;
        line-height: 4rem;
        font-size: 3rem;
        text-transform: uppercase;
        margin: 0.05rem;
        transform: translateY(-200%);
        color: black;
        font-family: Vollkorn;
    }
}
