.link-container {
    display: flex;
    flex-direction: column;
    padding-right: 2rem;
    height: 26rem;
    justify-content: flex-end;
}

.about {
    /* prevents overlapping at about conatiner */
    margin-top: 10rem;
}
.link-list {
    z-index: 3;
}

ul > a {
    text-decoration: none;
}

ul li {
    height: 5rem;
    display: flex;
    overflow: hidden;
    margin-bottom: 0.05rem;
    justify-content: flex-end;
}

ul li span {
    display: block;
    line-height: 4.2rem;
    font-size: 3rem;
    text-transform: uppercase;
    margin: 0.05rem;
    transform: translateY(200%);
    color: black;
}

ul li span.active {
    transform: translateY(0%);
    transition: 2s ease-out;
}
ul li:hover {
    opacity: 0.5;
    cursor: pointer;
}

.navigation {
    padding: 2rem;
}

.list span {
    font-size: 2rem;
}

.header2 span {
    font-size: 2rem !important;
}

@media only screen and (max-width: 820px) {
    .link-container {
        height: 12rem;
        bottom: 1rem;
        right: 1rem;
    }
    ul li {
        font-size: 1rem;
    }
    ul li span {
        line-height: 1rem;
        font-size: 1rem;
    }

    ul .list span {
        font-size: 1rem;
        line-height: 1rem;
    }
}
