.admin-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.admin {
    top: 0;
    left: 0;
    display: flex;
    position: sticky;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
    background-color: white;
    height: 2rem;
}
.admin > a {
    padding: 1rem;
    color: black;
}

.admin-container:only-child {
    margin: 2rem;
}
.admin-header {
    margin: 1rem;
}
.login {
    min-width: 7rem;
    padding: 0.5rem;
}
.login:hover {
    background-color: var(--var-color_two);
    color: white;
}
input {
    margin: 0.6rem;
    font-size: 1.2rem;
    width: 300px;
}
textarea {
    margin: 1rem;
    width: 300px;
    height: 150px;
}
