:root {
    --var-color-one: rgb(255 172 18);
    --var-color_two: #161616;
    --var-color_three: #ffffee;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Montserrat;
    list-style: none;
    font-size: 16px;
}

body {
    background-color: var(--var-color-one);
    transition: all 1s ease;
    width: 100vw;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

article {
    font-family: Montserrat;
}

.section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    padding: 1rem;
    height: 100vh;
    background-color: var(--var-color_three);
}

.project-inner-section {
    width: 100vw;
    background-color: var(--var-color_three);
}

section img {
    width: 90%;
    height: 50%;
    object-fit: cover;
}

.page2 {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    width: 100vw;
    margin-top: 2rem;
}
.page2 h1 span {
    margin-bottom: 1.4rem;
    font-size: 2rem;
}

.bg-active {
    background-color: var(--var-color_three);
}
@media only screen and (max-width: 850px) {
    .section {
        margin-top: 23rem;
    }
}
