.project-section {
    /* border: 1px solid blue; */
    height: 250px;
    align-items: center;
}
.single {
    margin-top: 25rem;
}
.project-inner-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: space-evenly;
    margin-top: 3rem;
    width: 100vw;
    background-color: var(--var-color_three);
}
.toggle-button {
    margin: 0 auto;
    display: flex;
    justify-content: center;
    font-size: 1.3rem;
    height: 2rem;
    align-items: center;
    border: 2px solid black;
    max-width: 20rem;
    text-decoration: none;
    background-color: beige;
}
.toggle-button:hover {
    background-color: var(--var-color-one);
}
.toggle-link {
    text-decoration: none;
    text-transform: lowercase;
    color: black;
}
.project-inner-section:last-child {
    padding-bottom: 3rem;
}
article {
    padding: 0 3rem 3rem 3rem;
}

.article-main-header h1 span {
    font-size: 3rem;
}

.article-image-container {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    width: 90%;
    margin: 2rem auto;
    height: 310px;
}

.article-image {
    height: 300px;
    object-fit: scale-down;
}
.article-header {
    padding-top: 2.5rem;
    text-align: center;
}
.title2 {
    padding-top: 0;
    height: 5rem !important;
}
.article-headline {
    display: flex;
    font-family: Vollkorn;
    padding: 0.5rem;
    padding-left: 0;
    margin-left: -1rem;
}
.article-content {
    text-align: center;
}
.description {
    text-align: start;
}

.article-body:last-child {
    padding-bottom: 0;
}

.shrink {
    animation: shrink 2s;
}
@keyframes shrink {
    from {
        opacity: 0.4;
        height: 200px;
    }
    to {
        opacity: 1;
        height: 300px;
    }
}

@media only screen and (max-width: 850px) {
    .modal-content {
        display: block;
        width: 100%;
        background-color: white;
        padding-top: 1rem;
        overflow-x: hidden;
        overflow-y: scroll;
    }
    .toggle-button {
        font-size: 1rem;
    }
}
